* {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  outline: none;
}

.wrapper {
  max-width: 50%;
  margin: 50px auto;
 
}

.join-block {
  margin: 0 auto;
  width: 300px;
}

.join-block input {
  font-size: 24px;
  width: 100%;
  height: 50px;
  padding-bottom: 5px;
  padding-left: 10px;
  margin-bottom: 10px;
}

.join-block button {
  width: 100%;
  padding: 10px;
  margin-top: 10px;
  font-weight: bold;
  cursor: pointer;
}


.chat {
  display: flex;
  height: 500px;
  border: 1px solid rgba(52, 57, 59, 0.2);
  border-radius: 8px;
  overflow: hidden;
}



  
.chat-users {
  border-right: 1px solid rgba(31, 33, 34, 0.1);
  padding: 20px;
  width: 200px;
  background-color: #f6f9fa;
}

.chat-messages {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 1;
  padding: 30px;
}

.messages {
  flex: 1;
  height: calc(100% - 155px);
  overflow: auto;
}

.message {
  margin-bottom: 20px;
}

.message p {
  display: inline-flex;
  border-radius: 10px;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  background-color: #7160ff;
  padding: 10px 15px 15px;
  color: #fff;
  margin-bottom: 2px;
}

.message span {
  opacity: 0.5;
  font-size: 14px;
}

.chat-users ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.chat-users ul li {
  margin-top: 10px;
  border-radius: 3px;
  background-color: #fff;
  padding: 8px 14px;
  box-shadow: 0 3px 5px rgba(159, 183, 197, 0.1);
  font-weight: 500;
}

.chat-messages form {
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.chat-messages form textarea {
  width: 100%;
  margin-bottom: 10px;
}



@media(max-width: 1000px) {
  .chat{
    width:700px;
  }
  .wrapper{
    margin: 50px 50px;
  }
}




@media(max-width: 450px) {
  
  .chat{
    width:370px;
  }
  .wrapper{
    margin: 60px 0px 60px 60px;
  }
  .chat-users{
    width: 100px;
  }
  .chat-messages{
    width: 320px;
  }
}
@media(max-width: 350px) {
  .chat{
    width:320px;
  }
  .wrapper{
    margin: 50px 5px;
  }
  .chat-users{
    width: 100px;
  }
  .chat-messages{
    width: 300px;
  }
}


